<template>
    <div>
      <pageheader></pageheader>
      <div class="container">
        <div class="page-title-normal">
          <h2 class="page-title-h2"><span>check out</span></h2>
        </div>
        <!-- 进度条 -->
        <div class="check-step">
          <ul>
            <li class="cur">确认地址</li>
            <li class="cur">确认订单</li>
            <li class="cur">支付</li>
            <li class="cur">支付完成</li>
          </ul>
        </div>

        <div class="order-create">
          <div class="order-create-pic"><img src="/static/ok-2.png" alt=""></div>
          <div class="order-create-main">
            <h3>恭喜! <br>您的订单正在处理中!</h3>
            <p>
              <span>订单编号：{{orderId}}</span>
              <span>订单总金额：{{totalPrice | currency('￥')}}</span>
            </p>
            <div class="order-create-btn-wrap">
              <div class="btn-l-wrap">
                <router-link to="/cart" class="btn btn--m">购物车</router-link>
              </div>
              <div class="btn-r-wrap">
                <router-link to="/" class="btn btn--m">首页</router-link >
              </div>
            </div>
          </div>
        </div>
      </div>
     <pagefooter></pagefooter>
    </div>
</template>
<script>
 import pageheader from '@/components/pageheader.vue';
  import pagefooter from '@/components/pagefooter.vue';
  import breadcrumb from '@/components/breadcrumb.vue';
  // import modal from '@/components/modal.vue'
  import axios from 'axios';
    export default{
        data(){
            return{
              orderId:this.$route.query.orderId,
              totalPrice:this.$route.query.totalPrice,

            }
        },
      components:{
        pageheader:pageheader,
        pagefooter,
        breadcrumb,
        // modal
    },
    }
</script>



